<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;体检
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" :rules="rules" label-width="160px">
          <el-form-item label="标题" prop="title" :label-width="formLabelWidth">
            <el-input v-model="form.title" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="价格(元)" prop="price" :label-width="formLabelWidth">
            <el-input v-model="form.price" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="图片" class="padding" prop="img" :label-width="formLabelWidth">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="img"
              disabled=""
              :show-file-list="false"
              :on-preview="handlePictureCardPreview"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="支持分院" prop="fenyuan" :label-width="formLabelWidth">
            <el-input type="textarea" disabled="" v-model="form.fenyuan"  autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="概况" prop="info" :label-width="formLabelWidth">
            <el-input type="textarea"  v-model="form.description" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="详情" prop="description" :label-width="formLabelWidth">
            <UE :defaultMsg=defaultMsg :config=config ref="ue"></UE>
          </el-form-item>
          <!--          </el-form-item>-->
          <el-form-item class="submit">
<!--            <el-button type="primary" @click="onSubmit('form')">提交</el-button>-->
            <el-button @click="$router.go(-1)">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'
  import UE from '../../common/uediter/ue.vue'

  export default {
    name: 'gerenD',
    data: function () {
      return {
        img:window.HOST,
        imageUrl: '',
        masterImg: "",
        uploadUrl: window.HOST + "/resources/img",
        token: '',
        form: {},
        rules: {
          serviceTypeName: [{
            required: true,
            message: '请填写服务分类名称',
            trigger: 'blur'
          }],
          serviceTypePrice: [{
            required: true,
            message: '请填写服务分类价格',
            trigger: 'blur'
          }],
          // img:[{
          //   required: true,
          //   // message: '请上传服务分类图标',
          //   // trigger: 'blur'
          // }]
        },
        id: '',
        dialogVisible: false,
        dialogImageUrl: '',
        formLabelWidth: '100px',
        config: {
          initialFrameWidth: 831,
          initialFrameHeight: 350
        },
        defaultMsg: '',
        defaultMsg2: '',
      }
    },
    components: {
      UE
    },
    created() {

    },
    activated(){
      this.getV()
    },
    methods: {
      getV() {
        var that = this;
        var params = {
          'datoken': that.$route.query.datoken,
        };
        that.apiPost('/admin/djfwone', params).then((res) => {
          this.handelResponse(res, (data) => {
            that.form = data.data;
            that.imageUrl = that.form.minspic;
            that.$nextTick(() => {
              that.defaultMsg = data.data.info;
              that.$refs.ue.setContent(that.form.info);
            })
          })
        })
      },
      //上传图片
      handlePictureCardPreview(file) {
        this.form.imgUrl = file.url;
        this.imageUrl = file.url;
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = res.data;
        this.form.imgUrl = res.data;
        // this.imageUrl = this.form.bPic;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type == 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;

        /*if(!isJPG) {
        	this.$message.error('上传正确格式图片!');
        }*/
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 2MB!');
        }
        return isLt2M;
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if(valid) {
            var that = this;
            if (that.form.imgUrl==null){
              that.$message.error('请上传服务分类图标')
              return false;
            } else {
              var params = that.form;
              that.apiPost('/serving/serviceTypeAdd', params).then((res) => {
                this.handelResponse(res, (data) => {
                  that.$message.success('成功');
                  that.$router.go(-1);
                })
              })
            }
          } else {
            return false;
          }
        });

      }
    },
    mixins: [http]
  }
</script>
<style>
  .el-upload--text {
    width: 180px !important;
  }
  .el-upload--text img {
    width: 100%;
    height: 100%;
  }
